/* clearfix */
.row {
  margin-top: -60px;
  margin-left: -15px;
  margin-right: -15px;
  max-width: none; }
  @media (min-width: 768px) {
    .row {
      margin-top: -60px; } }
  @media (min-width: 1200px) {
    .row {
      margin-top: -80px; } }
  .row:after {
    content: " ";
    display: block;
    clear: both; }
  .row + .row,
  .row .row {
    margin-top: 0; }
  .row--reducedWidth {
    margin-left: calc(6.25% - 15px);
    margin-right: calc(6.75% - 15px); }
    @media (min-width: 768px) {
      .row--reducedWidth {
        margin-left: calc(1.96% - 15px);
        margin-right: calc(4.3% - 15px); } }
